import React from "react";
import Card from "react-bootstrap/Card";
import "../../style.css";
import "bootstrap/dist/css/bootstrap.min.css";

function AboutCard() {
  return (
    <Card className="quote-card-view">
      <Card.Body>
        <blockquote className="blockquote mb-0">
          <p style={{ textAlign: "justify" }}>
            Hi Everyone, I am <span className="purple">Rashadul Islam </span>
            from <span className="purple"> Dhaka, Bangladesh.</span>
            <br />A self-motivated professional with a proven track record of exceeding
                performance targets. The ability to resolve complex problem with creative ideas. In-depth knowledge of web based applications. Able to learn quickly, analyze information and come up with innovative
                improvements.
            <br />
            <br />
            Apart from coding, some other activities that I love to do!
          </p>
          <ul>
            <li className="about-activity">
              <i className="far fa-hand-point-right"></i> Playing Games
            </li>
            <li className="about-activity">
              <i className="far fa-hand-point-right"></i> Reading Books
            </li>
            <li className="about-activity">
              <i className="far fa-hand-point-right"></i> Travelling
            </li>
          </ul>

          <p style={{ marginBlockEnd: 0, color: "rgb(155 126 172)" }}>
            "Strive to build things that make a difference!"{" "}
          </p>
          <footer className="blockquote-footer">Rashadul</footer>
        </blockquote>
      </Card.Body>
    </Card>
  );
}

export default AboutCard;
